   word-wrap: normal;
   line-height: 1.5;
 
   -moz-tab-size: 4;
   tab-size: 4;
 
   -webkit-hyphens: none;
   -ms-hyphens: none;
   hyphens: none;
 
 }
 
 /* Code blocks */
 pre[class*="lang-"] {
   padding: 1em;
   margin: .5em 0;
   overflow: auto;
   border-radius: 8px;
 }
 
 :not(pre) > code[class*="lang-"],
 pre[class*="lang-"] {
   background: #2d2d2d;
 }
 
 /* Inline code */
 :not(pre) > code[class*="lang-"] {
   padding: .1em;
   border-radius: .3em;
   white-space: normal;
 }
 
 .token.comment,
 .token.block-comment,
 .token.prolog,
 .token.doctype,
 .token.cdata {
   color: #999;
 }
 
 .token.punctuation {
   color: #ccc;
 }
 
 .token.tag,
 .token.attr-name,
 .token.namespace,
 .token.deleted {
   color: #e2777a;
 }
 
 .token.function-name {
   color: #6196cc;
 }
 
 .token.boolean,
 .token.number,
 .token.function {
   color: #f08d49;
 }
 
 .token.property,
 .token.class-name,
 .token.constant,
 .token.symbol {
   color: #f8c555;
 }
 
 .token.selector,
 .token.important,
 .token.atrule,
 .token.keyword,
 .token.builtin {
   color: #cc99cd;
 }
 
 .token.string,
 .token.char,
 .token.attr-value,
 .token.regex,
 .token.variable {
   color: #7ec699;
 }
 
 .token.operator,
 .token.entity,
 .token.url {
   color: #67cdcc;
 }
 
 .token.important,
 .token.bold {
   font-weight: bold;
 }
 .token.italic {
   font-style: italic;
 }
 
 .token.entity {
   cursor: help;
 }
 
 .token.inserted {
   color: green;
 }
.bd-main {
  grid-template-areas:
  "intro toc"
  "content toc";
  grid-template-columns: 4fr 1fr;
  grid-template-rows: auto 1fr;
  display: grid;
  grid-gap: inherit;
  gap: inherit;
}
.bd-toc {
  position: sticky;
  top: 1rem;
  z-index: 2;
  right: 0;
  grid-area: toc; 
  height: calc(100vh - 15rem);
  overflow-y: auto;
}
.bd-content {
  grid-area: content;
}
.bd-toc nav li {
  margin-bottom: .25rem;
}
.bd-toc nav ul {
  padding-left: 0;
  list-style: none;
}
.bd-toc nav a {
  color: inherit;
}
.bd-toc nav ul ul {
  padding-left: 1rem;
  margin-top: .25rem;
}
.empty-area {
  height: 500px;
}
.resume h2 {
  color: #444;
  font-size: 30px;
  margin: 20px 0px;
  text-align: center;
  text-transform: uppercase;
}
.resume table tr th{
  text-align: center;
  vertical-align: middle;
  font-size: 13px;
}
.resume table thead{
  background-color: #f2f2f2;
}
.resume table tr td{
  vertical-align: middle;
}
.content-dimmed{
  height: 100%;
  width: 100%;
  background-color: #e4e4e4;
  opacity: 0.9;
  z-index: 25000;
  position: absolute;
  left: 303px;
  top: 72px;
}
.overlay {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  background-color: rgb(0,0,0);
  background-color: rgba(0,0,0, 0.9);
  overflow-x: hidden;
  transition: 0.5s;
}

.overlay-content {
  position: relative;
  top: 25%;
  width: 100%;
  text-align: center;
  margin-top: 30px;
}

.overlay a {
  padding: 8px;
  text-decoration: none;
  font-size: 36px;
  color: #818181;
  display: block;
  transition: 0.3s;
}

.overlay a:hover, .overlay a:focus {
  color: #f1f1f1;
}

.overlay .closebtn {
  position: absolute;
  top: 20px;
  right: 45px;
  font-size: 60px;
}

@media screen and (max-height: 450px) {
  .overlay a {font-size: 20px}
  .overlay .closebtn {
  font-size: 40px;
  top: 15px;
  right: 35px;
  }
}
.message-p {
  color: #f1f1f1;
  display: block;
}
